* {
  color: #333; }

div#wrapper {
  display: flex;
  flex-direction: row; }

aside {
  display: flex;
  flex-direction: column;
  flex: 0 0 250px;
  padding: 2rem 2rem; }

main {
  display: flex;
  flex-direction: column;
  flex: 10;
  padding: 2rem 2rem; }

.h1, .h2, h1, h2 {
  display: block;
  font-weight: 600; }

.h1, h1 {
  margin-bottom: 30px;
  margin-top: 0;
  font-size: 30px; }

.h2, h2 {
  margin-top: 4px;
  margin-bottom: 18px;
  font-size: 22px; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.callout {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #7f8c8d;
  border-left-width: 5px; }
  .callout .h3 {
    color: #121212;
    font-weight: bold; }

.callout-info {
  border-left-color: #1b809e; }

.callout-danger {
  border-left-color: #ce4844; }

.page-description {
  margin-bottom: 40px;
  line-height: 23px; }

.spoiler:before {
  display: block;
  float: left;
  background: url(/public/images/common/spoiler.png) left top no-repeat;
  width: 16px;
  height: 16px;
  content: ' '; }

.spoiler .spoiler-head {
  user-select: none;
  color: #7f8c8d;
  text-decoration: underline;
  cursor: pointer; }

.spoiler .spoiler-body {
  margin-top: 5px;
  margin-bottom: 10px;
  display: none;
  border: 1px solid #2B2B2B;
  padding: 5px; }

.spoiler.spoiler-open .spoiler-body {
  display: block; }

.spoiler.spoiler-open:before {
  background: url(/public/images/common/spoiler.png) left bottom no-repeat; }

header {
  display: flex;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  padding: 25px 60px;
  line-height: 44px;
  flex-flow: row;
  z-index: 999; }
  header #mobile-menu {
    display: none; }
  header h1 {
    margin: 0; }
  header #left, header #right {
    height: 44px; }
  header #left {
    flex: 1 0;
    order: 1; }
    header #left h1 a {
      text-decoration: none; }
  header #right {
    display: flex;
    flex: 1 auto;
    order: 2;
    justify-content: flex-end; }
    header #right nav ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      flex-direction: row; }
      header #right nav ul li {
        margin: 0 12px; }
        header #right nav ul li a {
          text-decoration: none;
          font-size: 15px;
          padding-bottom: 3px; }
          header #right nav ul li a:hover, header #right nav ul li a.active {
            border-bottom: 2px solid #ff8f34; }
        header #right nav ul li input {
          height: 30px;
          line-height: 30px;
          box-sizing: border-box;
          font-size: 13px;
          padding: 0 15px 0 30px;
          border: 1px solid #e3e3e3;
          outline: none;
          border-radius: 15px;
          transition: border-color 0.2s ease;
          background: #fff url("../images/common/search.png") 8px 5px no-repeat;
          background-size: 20px; }

aside#sidebar.fix div#nav {
  position: fixed;
  width: 250px;
  top: 2rem; }

aside#sidebar nav ul {
  margin: 0;
  padding: 0;
  list-style: none; }
  aside#sidebar nav ul li a {
    line-height: 27px;
    height: 27px;
    text-decoration: none;
    color: #7f8c8d;
    font-size: 15px; }
    aside#sidebar nav ul li a:hover, aside#sidebar nav ul li a.active {
      border-bottom: 2px solid #ff8f34; }
    aside#sidebar nav ul li a.active {
      font-weight: bold; }
  aside#sidebar nav ul li ul li {
    padding-left: 30px; }
    aside#sidebar nav ul li ul li a {
      color: #7f8c8d;
      font-size: 14px;
      line-height: 23px;
      height: 23px; }

aside#sidebar .h2:not(:first-of-type) {
  margin-top: 25px; }

aside#sidebar #about {
  font-size: 14px; }
  aside#sidebar #about p {
    color: #7f8c8d; }

.breadcrumbs {
  display: block;
  list-style: none;
  margin: 0 0 30px;
  padding: 0; }
  .breadcrumbs li {
    float: left;
    font-size: 16px; }
    .breadcrumbs li a {
      text-decoration: none; }
      .breadcrumbs li a span {
        font-weight: normal; }
        .breadcrumbs li a span:hover {
          text-decoration: underline; }
    .breadcrumbs li span {
      font-weight: bold; }
      .breadcrumbs li span.divider {
        padding: 0 5px; }

#image-holder {
  position: fixed;
  bottom: 0;
  right: 0;
  top: 0;
  left: 0;
  width: auto;
  height: auto;
  display: block;
  overflow: hidden;
  z-index: 8010;
  background-color: rgba(0, 0, 0, 0.5); }
  #image-holder img {
    border: none;
    margin: auto;
    display: block;
    left: 50%;
    top: 50%;
    position: absolute;
    box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.75); }

.category-article:not(:last-of-type) {
  margin-bottom: 50px; }

.article-logo {
  float: left;
  margin-bottom: 10px;
  margin-right: 20px; }

.article h1 {
  margin-bottom: 10px; }

.article-body img {
  display: block;
  border: 1px solid #333;
  max-width: 100%;
  height: auto; }

.article-author, .article-date {
  display: inline-block;
  font-size: 14px;
  color: #7f8c8d;
  margin-bottom: 10px; }

ul.pagination {
  list-style: none;
  padding: 0;
  text-align: center;
  font-size: 20px;
  line-height: 28px;
  margin: 40px 0 20px; }
  ul.pagination li {
    display: inline-block;
    width: 28px;
    height: 28px; }
    ul.pagination li.disabled span {
      color: #7f8c8d; }
    ul.pagination li.active span {
      color: #000; }
    ul.pagination li a, ul.pagination li span {
      display: block;
      text-decoration: none; }
    ul.pagination li span {
      font-weight: bold; }

@media screen and (max-width: 768px) {
  header {
    position: fixed;
    width: calc(100% - 15px);
    top: 0;
    left: 0;
    height: 40px;
    background-color: #fff;
    padding: 15px; }
    header #left {
      text-align: center; }
      header #left #mobile-menu {
        display: block !important;
        width: 40px;
        height: 40px;
        top: 14px;
        left: 12px;
        position: absolute;
        background: url("../images/common/menu.png") center center no-repeat;
        background-size: 40px; }
    header #right {
      display: none; }
  aside#sidebar {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
    top: 0;
    left: 0; }
    aside#sidebar .open {
      display: block;
      background-color: #fff;
      position: fixed; }
  main {
    padding-top: 120px; }
  aside#sidebar {
    display: none; }
  aside#content {
    padding-right: 10px;
    padding-left: 10px; }
  .article-body img {
    width: 100%;
    height: auto; } }

@media all and (max-width: 425px) {
  main {
    flex-direction: column; }
  aside#content, aside#sidebar {
    order: 0; }
  aside#sidebar, header, footer {
    min-height: 50px;
    max-height: 50px; } }
