header {
  display: flex;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  padding: 25px 60px;
  line-height: 44px;
  flex-flow: row;
  z-index: 999;

  #mobile-menu {
    display: none;
  }

  h1 {
    margin: 0;
  }

  #left, #right {
    height: 44px;
  }

  #left {
    flex: 1 0;
    order: 1;

    h1 {
      a {
        text-decoration: none;
      }
    }
  }

  #right {
    display: flex;
    flex: 1 auto;
    order: 2;
    justify-content: flex-end;

    nav {
      ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: row;

        li {
          margin: 0 12px;

          a {
            text-decoration: none;
            font-size: 15px;
            padding-bottom: 3px;

            &:hover, &.active {
              border-bottom: 2px solid $detail-color;
            }
          }

          input {
            height: 30px;
            line-height: 30px;
            box-sizing: border-box;
            font-size: 13px;
            padding: 0 15px 0 30px;
            border: 1px solid #e3e3e3;
            outline: none;
            border-radius: 15px;
            transition: border-color 0.2s ease;
            background: #fff url("../images/common/search.png") 8px 5px no-repeat;
            background-size: 20px;
          }
        }
      }
    }
  }
}